import { Injectable } from '@angular/core';
import { Observable, from, of } from 'rxjs';
import { sp, WebEnsureUserResult } from '@pnp/sp';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'bluebird';

@Injectable({
  providedIn: 'root'
})
export class SecurityService {

    constructor(private httpClient: HttpClient) { }

    getCurrentUserGroups(): any {
        console.log('------getCurrentUserGroups------');
        let path: string = '/_api/web/lists/slEmployees/currentGroups';
        let httpParams = new HttpParams();

        let httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json; charset=utf-8'
            }),
            params: httpParams
        }
        return this.httpClient.get(path, httpOptions);

        return from(sp.web.currentUser.groups.usingCaching().get().then((groups: any[]) => groups.map(i => i.LoginName)));
    }

    ensureUser(login: string): Observable<WebEnsureUserResult> {
        console.log('------ensureUser------');
        return from(sp.web.ensureUser(login));
    }
}
