import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { NgZone } from '@angular/core';
import { Router } from '@angular/router';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';
import { Employee } from 'src/app/staff/services/employees/employee';
import { CompaniesService } from 'src/app/services/companies/companies.service';
import { Company } from 'src/app/services/companies/company';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {
  public photo = null;
  public logotype: string = null;
  public route: string[] = [];
  public root: string = null;
  public reason: string = null;
  public isLocationVisible = false;
  public searchIsVisible: Boolean = false;
  public activityFeedIsVisible: Boolean = false;
  private companyChangeSubscribe: Subscription = null;

  constructor(
    private sidebarService: SidebarService,
    @Inject('EmployeesFactory') private employeesFactory: EmployeesFactory,
    private sharepointService: SharepointService,
    private companiesService: CompaniesService,
    private zone: NgZone,
    private router: Router
  ) {}

  handleToggleActivityFeed() {
    this.sidebarService.handleToggleActivity();
  }

  handleToggleSearch() {
    this.searchIsVisible = !this.searchIsVisible;
  }

  toggleLocation() {
    this.sidebarService.handleToggleLocation();
  }

  handleToggleSiteMap() {
    this.sidebarService.handleToggleSiteMap();
  }

  handleToggleSidebar() {
    this.sidebarService.handleToggleSidebar();
  }

  handleToggleFullServices() {
    this.sidebarService.handleToggleFullServices();
  }

  public prepareLink(link: string) {
    return (
      (link &&
        link
          .replace(/\(/gi, '\\(')
          .replace(/\)/gi, '\\)')
          .replace(/'/gi, '%27')
          .replace(/ /gi, '%20')) ||
      null
    );
  }

  ngOnDestroy(): void {
    if (this.companyChangeSubscribe != null) {
      this.companyChangeSubscribe.unsubscribe();
    }
  }

  ngOnInit() {
    this.root =
      this.sharepointService.getCurrentUserContext().webServerRelativeUrl +
      '/PortalPages/home.aspx#/company';
    this.employeesFactory
      .getCurrentEmployee()
      .subscribe((employee: Employee) => {
        if (employee != null) {
          this.photo = employee.photo;
          this.route = employee.profileRoute;
          this.reason = employee.reason;
        }
      });
    this.companyChangeSubscribe = this.companiesService.companyChangeEvent.subscribe(
      (c: Company) => {
        if (c && c.logotype !== null) {
          this.logotype = this.prepareLink(c.logotype);
        } else {
          this.logotype = null;
        }
      }
    );
    this.sidebarService.toggleActivity.subscribe((isVisible: boolean) =>
      this.zone.run(() => {
        this.activityFeedIsVisible = isVisible;
      })
    );
    this.sidebarService.toggleLocation.subscribe((isVisible: boolean) =>
      this.zone.run(() => {
        this.isLocationVisible = isVisible;
      })
    );
  }

  goToRoot() {
    this.zone.run(() => {
      if (
        window.location.href.indexOf('/PortalPages/') > 0 &&
        this.root.indexOf('#') > 0
      ) {
        this.router.navigate([this.root.split('#')[1]]);
      } else {
        window.location.href = this.root;
      }
    });
  }
}
