import { Injectable, Inject } from '@angular/core';
import { UserContext } from './user-context';
import { DOCUMENT } from '@angular/common';
import { Web, sp, OpenWebByIdResult } from '@pnp/sp';
import { Observable, from, of } from 'rxjs';
import { map } from 'rxjs/operators';

// src\js\sp.js
declare function getUserContext(): UserContext;
declare function fixRibbonAndWorkspaceDimensionsForResize(): void;

@Injectable({
  providedIn: 'root'
})
export class SharepointService {

  constructor(@Inject(DOCUMENT) private document) { }

  public getCurrentUserContext(): UserContext {
    const context: UserContext = getUserContext();
    context.webServerRelativeUrl = context.webServerRelativeUrl === '/' ? '' : context.webServerRelativeUrl;
    return context;
  }

  public getRelativeUrl(): string {
    return getUserContext().webServerRelativeUrl;
  }

  public getAbsoluteUrl(): string {
    return getUserContext().webAbsoluteUrl;
  }

  public hideRibbon(): void {
    // this.document.getElementById('s4-ribbonrow').setAttribute('style', 'display:none');
    // this.document.getElementById('suiteBarDelta').setAttribute('style', 'display:none');
    // fixRibbonAndWorkspaceDimensionsForResize();
  }

  public showRibbon(): void {
    // this.document.getElementById('s4-ribbonrow').setAttribute('style', 'display:block');
    // this.document.getElementById('suiteBarDelta').setAttribute('style', 'display:block');
    // fixRibbonAndWorkspaceDimensionsForResize();
  }

  public getWebIdByRelativeUrl(relativeUrl: string): Observable<string> {
    while (relativeUrl.charAt(0) === '/') {
      relativeUrl = relativeUrl.substr(1);
    }
    const path = window.location.origin + this.getRelativeUrl() + '/' + relativeUrl;
    const web = new Web(path);
    return from(web.select('Id').get()).pipe(map(w => w.Id));
  }
}
