import { Injectable, Inject } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { Employee } from 'src/app/staff/services/employees/employee';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';

@Injectable()
export class BirthdaysResolver implements Resolve<IListItemsPage<Employee>> {
  constructor(@Inject('EmployeesFactory') private employeesFactory: EmployeesFactory) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IListItemsPage<Employee>> {
    const month = new Date().getMonth() + 1;
    return this.employeesFactory.getPagedByMonthBirthday(month);
  }
}
