import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';

import { EmployeesFactory } from './employees.factory';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { map } from 'rxjs/operators';
import { EmployeesService } from './employees.service';
import { MsalSettingsService } from '../msal-settings/msal-settings.service';
import { Employee } from './employee';
import { AdalSettingsService } from '../adal-settings/adal-settings.service';

@Injectable()
export class WsEmployeesService implements EmployeesFactory {

  private size = 15;

  constructor(private http: HttpClient,
    private employeesService: EmployeesService,
    private msalSettingsService: MsalSettingsService,
    private adalSettingsService: AdalSettingsService) { }

  getCurrentEmployee(): Observable<Employee> {
    return this.http.get(this.msalSettingsService.getCurrentEmployeeUrl(), {
        params: {},
        headers: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
          'SharePointUrl': this.msalSettingsService.getPortalUrl(),
        },
      }
    ).pipe(
      map((response: any) => this.convertListItemToEntity(response))
    );
  }

  public convertListItemToEntity(item: any): Employee {
      const entity: Employee = {
        title: `${item.LastName} ${item.FirstName} ${item.FatherName}`,
        fullName: `${item.LastName} ${item.FirstName} ${item.FatherName}`,
        address: item.Address,
        birthdayDay: item.BirthdayDay,
        birthdayMonth: item.BirthdayMonth,
        city: item.City,
        lookupDepartmentId: item.DepartmentId,
        lookupDepartmentTitle: item.DepartmentName,
        email: item.Email,
        fatherName: item.FatherName,
        firstName: item.FirstName,
        id: item.Id,
        isManager: item.IsManager,
        lastName: item.LastName,
        login: item.Login,
        lookupManagerTitle: item.ManagerFullName,
        lookupManagerId: item.ManagerId,
        mobile: item.Mobile,
        officeNumber: item.OfficeNumber,
        phoneInternal: item.PhoneInternal,
        positionName: item.PositionName,
        rootLookupDepartmentId: item.RootDepartmentId,
        rootLookupDepartmentTitle: item.RootDepartmentName,
        startWorkDate: item.StartWorkDate,
        tabNumber: item.TabNumber
      };
      this.employeesService.processEntity(entity);
      return entity;
  }

  search(searchText?: string, departmentId?: number, isFavorite?: boolean, size?: number, page?: number)
    : Observable<IListItemsPage<Employee>> {
      if (page == null) {
        page = 1;
      }
      const params: any = {
        'SearchString': searchText || '',
        'DepartmentId': departmentId && departmentId.toString() || '',
        'FavoriteOnly': isFavorite && isFavorite.toString() || 'false',
        'PageIndex': page.toString(),
        'PageSize': (size && size.toString()) || this.size.toString()
      };
      return this.http.get(this.msalSettingsService.getEmployeesBySearch(), {
          params: params,
          headers: {
            'Accept': 'application/json',
            'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
            'SharePointUrl': this.msalSettingsService.getPortalUrl(),
          },
        }
      ).pipe(
        map((response: any[]) => {
          const items: Employee[] = [];
          response.forEach(e => items.push(this.convertListItemToEntity(e)));
          return {items: items, hasNext: false, getNext: () => this.search(searchText, departmentId, isFavorite, size + 1, page)};
        })
      );
  }

  getPagedByMonthBirthday(month: number, index?: number, size?: number): Observable<IListItemsPage<Employee>> {
    if (index == null) {
      index = 1;
    }
    return this.http.get(this.msalSettingsService.getEmployeesByMonthBirthdayUrl(), {
        params: {
          'MonthNumber': month.toString(),
          'DepartmentId': null,
          'PageIndex': index.toString(),
          'PageSize': (size && size.toString()) || this.size.toString()
        },
        headers: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
          'SharePointUrl': this.msalSettingsService.getPortalUrl(),
        },
      }
    ).pipe(
      map((response: any[]) => {
        const items: Employee[] = [];
        response.forEach(e => items.push(this.convertListItemToEntity(e)));
        return {items: items, hasNext: false, getNext: () => this.getPagedByMonthBirthday(month, index + 1, size)};
      })
    );
  }

  addFavorite(id: number): Observable<void> {
    return this.http.post(this.msalSettingsService.getEmployeesInsertFavorites(), {
        body: {
          'params': {ContactIds: [id]}
        },
        headers: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
          'SharePointUrl': this.msalSettingsService.getPortalUrl(),
        },
      }
    ).pipe(
      map((response: any[]) => {})
    );
  }

  removeFavorite(id: number): Observable<void> {
    return this.http.post(this.msalSettingsService.getEmployeesRemoveFavorites(), {
        body: {
          'params': {ContactIds: [id]}
        },
        headers: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
          'SharePointUrl': this.msalSettingsService.getPortalUrl(),
        },
      }
    ).pipe(
      map((response: any[]) => {})
    );
  }

  getEmployeeById(id: number): Observable<Employee> {
    return this.http.get(this.msalSettingsService.getEmployeesById(), {
        params: {
          'Id': id.toString()
        },
        headers: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
          'SharePointUrl': this.msalSettingsService.getPortalUrl(),
        },
      }
    ).pipe(
      map((response: any) => this.convertListItemToEntity(response))
    );
  }

  getEmployeeByLogin(login: string): Observable<Employee> {
    return this.http.get(this.msalSettingsService.getEmployeesByLogin(), {
      params: {
        'Login': login
      },
      headers: {
        'Accept': 'application/json',
        'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
        'SharePointUrl': this.msalSettingsService.getPortalUrl(),
      },
    }
  ).pipe(
    map((response: any) => this.convertListItemToEntity(response))
  );
  }
}
