
import { Injectable } from '@angular/core';
import { ICachingOptions } from '@pnp/odata';
import { Observable, of } from 'rxjs';
import { flatMap, map } from 'rxjs/operators';
import * as moment from 'moment';

import { ListItemsService } from '../list-items-service';
import { SettingsValue } from './settings-value';
import { SharepointService } from '../sharepoint/sharepoint.service';

@Injectable({
  providedIn: 'root'
})
export class SettingsService extends ListItemsService<SettingsValue> {

  constructor(private sharepointService: SharepointService) {
    super();
  }

  getListTitle(): string {
    return 'Настройки портала';
  }

  getListRelativePath(): string {
    return 'lists/slSettings';
  }

  getCachingOptions(): ICachingOptions {
    return {
      expiration: moment().add(30, 'minutes').toDate(),
      key: this.getListTitle(),
      storeName: 'session'
    };
  }

  getSelect(): string {
    return 'Id,Title,slCode,slValue';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): SettingsValue  {
    const settings: SettingsValue = {
      id: item.Id,
      title: item.Title,
      code: item.slCode,
      value: item.slValue
    };
    return settings;
  }

  convertEntityToListItem(entity: SettingsValue) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): any[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  private getItemByCode(code: string): Observable<string> {
    return this.getItems().pipe(
      map((items: SettingsValue[]) => {
        console.log("------SETTINGS getItemByCode before------", items);
        items = items.filter(item => item.code === code);
        console.log("------SETTINGS getItemByCode after------", items);
        let value: string = null;
        if (items.length > 0) {
          value = items[0].value;
        }
        return value;
      })
    );
  }

  getHelpLink(): Observable<string> {
    return this.getItemByCode('HELP-LINK');
  }

  getRssLink(): Observable<string> {
    return this.getItemByCode('RSS-LINK');
  }

  getStaffMode(): Observable<string> {
    return this.getItemByCode('STAFF-MODE');
  }

  getStaffUrl(): Observable<string> {
    return this.getItemByCode('STAFF-URL');
  }

  getStaffClientId(): Observable<string> {
    return this.getItemByCode('STAFF-CLIENT-ID');
  }

  getStaffAuthority(): Observable<string> {
    return this.getItemByCode('STAFF-AUTHORITY');
  }

  getStaffScope(): Observable<string> {
    return this.getItemByCode('STAFF-SCOPE');
  }

  getPortalUrl(): Observable<string> {
    return this.getItemByCode('PORTAL-URL').pipe(
      map(url => url || this.sharepointService.getAbsoluteUrl())
    );
  }
}
