import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';

import { IListItemsPage } from 'src/app/services/list-items-service';
import { MsalSettingsService } from '../msal-settings/msal-settings.service';
import { Department } from './department';
import { DepartmentsFactory } from './departments.factory';
import { DepartmentsService } from './departments.service';
import { map } from 'rxjs/operators';
import { AdalSettingsService } from '../adal-settings/adal-settings.service';

@Injectable()
export class WsDepartmentsService implements DepartmentsFactory {

  private size = 15;

  constructor(private http: HttpClient,
    private departmentsService: DepartmentsService,
    private msalSettingsService: MsalSettingsService,
    private adalSettingsService: AdalSettingsService) { }

  public convertListItemToEntity(item: any): Department {
      const entity: Department = {
        id: item.Id,
        title: item.Name,
        lookupManagerId: item.ManagerId,
        lookupManagerFullName: item.ManagerFullName,
        lookupManagerPosition: item.ManagerPosition,
        employeeCount: item.EmployeeCount,
        parentCode: item.ParentCode,
        parentId: item.ParentId,
        parentDepartmentId: item.ParentId,
        parentDepartmentTitle: item.ParentName
      };
      this.departmentsService.processEntity(entity);
      return entity;
  }

  getItemsByParentId(id?: number): Observable<Department[]> {
    return this.http.get(this.msalSettingsService.getDepartmentsByParentId(), {
        params: {
          'ParentId' : id && id.toString() || ''
        },
        headers: {
          'Accept': 'application/json',
          'Authorization': 'Bearer ' + this.adalSettingsService.getToken(),
          'SharePointUrl': this.msalSettingsService.getPortalUrl(),
        },
      }
    ).pipe(
      map((response: any) => (response.length != null ? response.map(r => this.convertListItemToEntity(r))
        : this.convertListItemToEntity(response)))
    );
  }
}
