import { Injectable, EventEmitter, Inject } from '@angular/core';
import { ICachingOptions } from '@pnp/odata';
import { flatMap, map } from 'rxjs/operators';
import { Observable, of, empty } from 'rxjs';

import * as moment from 'moment';

import { Company } from './company';
import { ListItemsService } from '../list-items-service';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';
import { Employee } from 'src/app/staff/services/employees/employee';
import { SharepointService } from '../sharepoint/sharepoint.service';


@Injectable({
  providedIn: 'root'
})
export class CompaniesService extends ListItemsService<Company>   {

  public companyChangeEvent: EventEmitter<Company> = new EventEmitter();

  private company: Observable<Company> = null;

  constructor(@Inject('EmployeesFactory') private employeesFactory: EmployeesFactory,
  private sharepointService: SharepointService) {
    super();
  }

  getCachingOptions(): ICachingOptions {
    return {
      expiration: moment().add(30, 'minutes').toDate(),
      key: this.getListTitle(),
      storeName: 'session'
    };
  }

  getListTitle(): string {
    return 'Предприятия';
  }

  getListRelativePath(): string {
    return 'Lists/slCompanies';
  }

  getSelect(): string {
    return 'Id,Title,slCode,slUrl,slImage';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['Id', true]];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): Company {
    const entity: Company = {
      id: item.Id,
      title: item.Title,
      code: item.slCode,
      url: item.slUrl,
      logotype: item.slImage
    };
    return entity;
  }

  convertEntityToListItem(entity: Company) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Company[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  getCompanyForCurrentUser(): Observable<Company> {
    return this.employeesFactory.getCurrentEmployee().pipe(
      flatMap((e: Employee) => this.getItems({ filter: `slCode eq '${e.company}'`})),
      map((c: Company[]) => c.length > 0 ? c[0] : null));
  }

  getCurrentCompany(): Observable<Company> {
    if (this.company == null) {
      this.company = this.getCompanyForCurrentUser();
      this.company.subscribe((company: Company) => this.companyChangeEvent.emit(company));
    }
    return this.company;
  }

  getWebIdByCompanyId(id: string): Observable<string> {
    if (id === 'root') {
      return of<undefined>(undefined);
    } else {
      return this.getItems({ filter: `Id eq '${id}'`})
        .pipe(map((c: Company[]) =>  c.length > 0 ? c[0] : null ),
        flatMap((c: Company) => this.sharepointService.getWebIdByRelativeUrl(c && c.url)));
    }
  }

  setCurrentCompany(company: Company) {
    this.company = of(company);
    this.companyChangeEvent.emit(company);
  }
}
