import { Injectable } from '@angular/core';

declare function notify(message: string, title: string, type: string): void;

@Injectable({
  providedIn: 'root'
})
export class NotificationsService {

  constructor() { }

  public info(message: string, title?: string): void {
    notify(message, title || 'Информация', 'info');
  }

  public warning(message: string, title?: string): void {
    notify(message, title || 'Предупреждение', 'warning');
  }

  public error(message: string, error?: any, title?: string): void {
    notify(message, title || 'Ошибка', 'error');
    if (error) {
      console.log(error);
    }
  }

  public success(message: string, title?: string): void {
    notify(message, title || 'Успешно', 'success');
  }
}
