import { Component, Input, Output, EventEmitter, AfterContentChecked } from '@angular/core';

@Component({
  selector: 'app-tab',
  templateUrl: './tab.component.html',
  styleUrls: ['./tab.component.scss']
})
export class TabComponent implements AfterContentChecked {
  @Input()
  tabTitle: string;
  @Input()
  active = false;
  @Output() viewInit: EventEmitter<null> = new EventEmitter();
  @Input()
  notification: number;

  constructor() {}

  ngAfterContentChecked() {
    this.viewInit.emit();
  }
}
