import { Component, OnInit, AfterViewInit, Input, Output, EventEmitter } from '@angular/core';
import { SecurityService } from 'src/app/services/security/security.service';
import { WebEnsureUserResult, SiteUserProps } from '@pnp/sp';
import { forkJoin } from 'rxjs';

declare function initializePeoplePicker(id, login, isMulti): any;
declare function setPeoplePickerLoginName(id, login): void;
declare function clearPeoplePicker(id): void;

@Component({
  selector: 'app-sp-user',
  templateUrl: './sp-user.component.html',
  styleUrls: ['./sp-user.component.scss']
})
export class SpUserComponent implements AfterViewInit {

  public id = '_' + Math.random().toString(36).substr(2, 9);
  @Input()
  public isMulti = false;
  @Input()
  public login = null;
  @Input() theme: string = null;
  @Output()
  public value: EventEmitter<SiteUserProps[]> = new EventEmitter();

  constructor(private securityService: SecurityService) {}

  ngAfterViewInit() {
    const peoplePicker = initializePeoplePicker(this.id, this.login, this.isMulti);
    const input = document.getElementById(this.id + '_TopSpan_HiddenInput');
    const that = this;
    Object.defineProperty(input, 'value', {
        get: function () { return this.defaultValue; },
        set: function (val) {
          this.defaultValue = val;
          const login = peoplePicker.GetAllUserKeys();
          if (login && login !== '') {
            if (this.value == null || this.value.LoginName !== login) {
              forkJoin(login.split(';').map(l => that.securityService.ensureUser(l)))
                .subscribe((users: WebEnsureUserResult[]) => that.value.emit(users.map(u => u.data)));
            }
          } else {
            that.value.emit([]);
          }
        }
    });
  }

  setLoginName(login) {
    setPeoplePickerLoginName(this.id, login);
  }

  clear() {
    clearPeoplePicker(this.id);
    this.value.emit([]);
  }
}
