import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { PlatformLocation } from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CookieService } from 'ngx-cookie-service';

import { RouterModule } from '@angular/router';


import { AppComponent } from './app.component';

import { HttpClientModule } from '@angular/common/http';

import { groups } from './constants/groups';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { SecurityService } from 'src/app/services/security/security.service';
import { HeaderComponent } from './core/header/header.component';
import { FooterComponent } from './core/footer/footer.component';
import { LeftMenuComponent } from './core/left-menu/left-menu.component';
import { CoreModule } from './core/core.module';
import { MsalSettingsService } from './staff/services/msal-settings/msal-settings.service';
import { StaffModule } from './staff/staff.module';
import { MsalFrameComponent } from './staff/components/msal-frame/msal-frame.component';

export function load(msalSettingsService: MsalSettingsService) {
  return (() => msalSettingsService.load());
}
@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    CoreModule,
    RouterModule,
    StaffModule,
    HttpClientModule
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: load,
      deps: [MsalSettingsService],
      multi: true
    },
    CookieService
  ],
  entryComponents: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    LeftMenuComponent,
    MsalFrameComponent
  ]
})
export class AppModule {

  constructor(private platformLocation: PlatformLocation,
    private sharepointService: SharepointService,
    private securityService: SecurityService) { }

  ngDoBootstrap(app) {
    if (window !== window.parent && !window.opener)
    {
      console.log('Bootstrap: MSAL');
      app.bootstrap(MsalFrameComponent);
    } else {
      console.log('Bootstrap: App');
      this.securityService.getCurrentUserGroups()
        .subscribe(g => {
          if (g && g.includes(groups.employee)) {
            this.sharepointService.hideRibbon();
          } else {
            this.sharepointService.showRibbon();
          }
        });

      if (this.platformLocation.pathname.toLowerCase().indexOf('portalpages') > 0) {
        console.log('Bootstrap: AppComponent');
        app.bootstrap(AppComponent);
      }
      console.log('Bootstrap: HeaderComponent');
      app.bootstrap(HeaderComponent);
      console.log('Bootstrap: LeftMenuComponent');
      app.bootstrap(LeftMenuComponent);
      console.log('Bootstrap: FooterComponent');
      app.bootstrap(FooterComponent);
    }
  }
}

