import { Injectable } from '@angular/core';

import * as moment from 'moment';
import { Employee } from './employee';

@Injectable()
export class EmployeesService {

  processEntity(entity: Employee): void {
    entity.photo = `/_layouts/15/userphoto.aspx?size=L&username=${entity.login}`;
    entity.profileRoute = ['/phonebook', entity.id.toString()];
    if (entity.birthdayMonth && entity.birthdayDay) {
      entity.birthdayTitle = this.formatDate(entity.birthdayMonth, entity.birthdayDay);
      entity.birthdayTitleRelative = this.formatDateWithOutReltive(entity.birthdayMonth, entity.birthdayDay);
    }
    if (entity.startWorkDate) {
      entity.startWorkDateTitle = moment(entity.startWorkDate).calendar(null, {
        sameDay: '[Сегодня]',
        nextDay: '[Завтра]',
        nextWeek: 'D MMMM',
        lastDay: '[Вчера]',
        lastWeek: 'D MMMM',
        sameElse: 'L'
      });
    }
    if (entity.startWorkDate) {
      entity.startdayTitleRelative = moment(entity.startWorkDate).calendar(null, {
        sameDay: 'D MMMM',
        nextDay: 'D MMMM',
        nextWeek: 'D MMMM',
        lastDay: 'D MMMM',
        lastWeek: 'D MMMM',
        sameElse: 'D MMMM'
      });
    }
  }

  private formatDate(month, day) {
    const year = new Date().getFullYear();
    const date = moment(`${day}.${month}.${year}`, 'DD.MM.YYYY');
    const result = date.calendar(null, {
      sameDay: '[Сегодня]',
      nextDay: '[Завтра]',
      nextWeek: 'D MMMM',
      lastDay: '[Вчера]',
      lastWeek: 'D MMMM',
      sameElse: 'D MMMM'
    });
    return result;
  }

  private formatDateWithOutReltive(month, day) {
    const year = new Date().getFullYear();
    const date = moment(`${day}.${month}.${year}`, 'DD.MM.YYYY');
    const result = date.calendar(null, {
      sameDay: 'D MMMM',
      nextDay: 'D MMMM',
      nextWeek: 'D MMMM',
      lastDay: 'D MMMM',
      lastWeek: 'D MMMM',
      sameElse: 'D MMMM'
    });
    return result;
  }
}
